function Cover_home() {
  const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());
  const [mobile, setMobile] = useState(false)

  function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    return {
      width,
      height
    };
  }

  useEffect(() => {
    function handleResize() {
      setWindowDimensions(getWindowDimensions());
    }

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    if (windowDimensions.width < 600) {
      if (!mobile) setMobile(true);
    }
    else {
      if (mobile) setMobile(false);
    }

  }, [windowDimensions])

  if (mobile) {
    return (
      <div className='cover'>
        <div className='cover-mobile'>
          <CoverBack />
          <div className='title'>
            <p style={{ fontSize: '3rem' }}>NCKU</p>
            <p>Neural Computation</p>
            <p>Laboratory</p>
            <div style={{ display: 'block', height: '5rem', width: '100%' }} />
          </div>
        </div>
      </div>
    )
  } else {
    return (
      <div className='cover'>
        <Container fluid className='cover-pc'>
          <Row className='w-100 h-100'>
            <Col xs={12} md={7} className='h-100'>
              <div className='title'>
                <p style={{ fontSize: '3rem' }}>NCKU</p>
                <p>Neural Computation</p>
                <p>Laboratory</p>
              </div>
            </Col>
            <Col xs={12} md={5} className='h-100'>
              <CoverBack />
            </Col>
          </Row>
        </Container>

        <div className='down-section'>
          <div className='animate__animated animate__bounce animate__infinite'>
            <BsChevronDoubleDown />
          </div>
        </div>
      </div>
    )
  }
}

