import { IoCubeOutline } from "react-icons/io5";
import { Container, Row, Col } from "react-bootstrap";
import React, { Component, useEffect, useState } from "react";





function Cover(props) {
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());
    const [mobile, setMobile] = useState(false)

    function getWindowDimensions() {
        const { innerWidth: width, innerHeight: height } = window;
        return {
            width,
            height
        };
    }

    useEffect(() => {
        function handleResize() {
            setWindowDimensions(getWindowDimensions());
        }

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    useEffect(() => {
        if (windowDimensions.width < 600) {
            if (!mobile) setMobile(true);
        }
        else {
            if (mobile) setMobile(false);
        }

    }, [windowDimensions])

    if (mobile) {
        return (


            <div className='page'>
                <div className='cover cover-mobile'>
                    <div className='title'>
                        <p style={{ fontSize: '3rem' }}>NCKU</p>
                        <p>Neural Computation</p>
                        <p>Laboratory</p>
                    </div>
                    <div className='mask' />
                    <div className='rec' />
                    <div className='subtitle'>
                        <p>{props.pageName.en}</p>
                    </div>

                </div>


                <Container className='pageTitle'>
                    <Row>
                        <Col xs={12} md={6}>
                            <div className='text'>
                                <p className='my-auto'><IoCubeOutline /></p>
                                <div style={{ width: '2rem', height: '1rem' }} />
                                <p className='my-auto'>{props.pageName.tw}</p>
                            </div>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={6}>

                            <div className='line' />

                        </Col>
                    </Row>

                </Container>
            </div>
        );
    } else {
        return (


            <div className='page'>
                <div className='cover cover-pc'>
                    <div className='title'>
                        <p style={{ fontSize: '3rem' }}>NCKU</p>
                        <p>Neural Computation</p>
                        <p>Laboratory</p>

                    </div>
                    <div className='mask' />
                    <div className='rec' />
                    <div className='subtitle'>
                        <p>{props.pageName.en}</p>
                    </div>

                </div>


                <Container className='pageTitle'>
                    <Row>
                        <Col xs={12} md={6}>
                            <div className='text'>
                                <p className='my-auto'><IoCubeOutline /></p>
                                <div style={{ width: '2rem', height: '1rem' }} />
                                <p className='my-auto'>{props.pageName.tw}</p>
                            </div>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={6}>

                            <div className='line' />

                        </Col>
                    </Row>

                </Container>
            </div>
        );
    }

}

export { Cover }